CI,iCAAiC;AACrC;AACA;IACI,sCAAsC;IACtC,kBAAkB;IAClB,YAAY;IACZ,YAAY;IACZ,iBAAiB;AACrB;;AAEA;IACI,sCAAsC;IACtC,YAAY;AAChB;AACA;IACI,WAAW;IACX,YAAY;IACZ,kBAAkB;IAClB,MAAM;IACN,OAAO;IACP,mBAAmB;AACvB","sourcesContent":["/* styles.css */\n*,\n*::before,\n*::after {\n    box-sizing: border-box;\n    margin: 0;\n    padding: 0;\n}\n:root {\n    /* --body-bg: linear-gradient(\n        to top,\n        rgba(0, 0, 0, 0.984),\n        rgba(0, 0, 0, 0.821)\n    ); */\n    --body-bg: linear-gradient(\n        to top,\n        rgb(0, 0, 0),\n        rgb(86, 86, 75),\n        rgb(0, 0, 0)\n    );\n    --body-bg-clouds: linear-gradient(\n        to top,\n        rgb(0, 0, 0),\n        rgb(52, 56, 70),\n        rgb(0, 0, 0)\n    );\n    --body-bg-rainy: linear-gradient(\n        to top,\n        rgb(0, 0, 0),\n        rgb(7, 35, 33),\n        rgb(0, 0, 0)\n    );\n    --grad-bg: linear-gradient(\n        to left,\n        rgba(113, 113, 28, 0.968),\n        rgb(72, 70, 51),\n        rgba(237, 237, 237, 0.162)\n    );\n    --grad-bg-clouds: linear-gradient(\n        to left,\n        rgb(0, 0, 0),\n        rgb(52, 56, 70),\n        rgb(0, 0, 0)\n    );\n    --grad-bg-rainy: linear-gradient(\n        to left,\n        rgb(0, 0, 0),\n        rgb(7, 35, 33),\n        rgb(0, 0, 0)\n    );\n    --accent-bg-clear: #3f3a2c;\n    --accent-bg-clouds: rgb(52, 56, 70);\n    --accent-bg-rainy: rgb(52, 70, 69);\n    --main-font-family: \"Poppins\", sans-serif;\n    --heading-font-family: \"Raleway\", sans-serif;\n    --container-bg: rgba(66, 66, 66, 0.135);\n    --border-radius: 1rem;\n    --base-font-size: clamp(10px, calc(6px + 0.5vw), 1.5vw);\n    --text-color: #ffffff;\n    --text-color-dup: #b6b6b6da;\n}\nhtml,\ninput {\n    font-size: var(--base-font-size);\n    font-family: var(--main-font-family);\n}\nbody {\n    height: 100vh;\n    width: 100vw;\n}\n\ninput {\n    font-weight: 500;\n    color: rgba(255, 255, 255, 0.83);\n}\nh1,\nh2,\nh3 {\n    color: var(--body-bg);\n}\n.container {\n    min-height: 100%;\n    min-width: 100vw;\n    display: grid;\n    grid-template-areas: header main footer;\n    grid-template-rows: 2.5fr 20fr 1fr;\n    background: var(--body-bg);\n    gap: 0rem;\n}\n.container:has(.temp#clouds) {\n    background: var(--body-bg-clouds);\n}\n.container:has(.temp#rain) {\n    background: var(--body-bg-rainy);\n}\n.container > * {\n    display: flex;\n}\n\nheader {\n    justify-content: space-between;\n}\nfooter {\n    justify-content: center;\n    align-items: center;\n}\n.sidebar {\n    max-width: 25rem;\n}\n.sidebar #fav-city {\n    display: flex;\n    flex-direction: column;\n    padding: 1rem;\n    margin-bottom: 2rem;\n    gap: 0.5rem;\n}\n#weekly-forecast {\n    display: flex;\n    position: relative;\n}\n#fav-city .city-list-item,\n#weekly-forecast .day-list-item {\n    list-style-type: none;\n    display: flex;\n    flex-direction: column;\n    gap: 0.1rem;\n    padding: 0.8rem;\n    border-radius: 0.8rem;\n}\n.forecast-list {\n    overflow: hidden;\n    /* Optional: Adds a faded edge for a nicer look */\n    mask: linear-gradient(\n        90deg,\n        transparent,\n        white 20%,\n        white 80%,\n        transparent\n    );\n    -webkit-mask: linear-gradient(\n        90deg,\n        transparent,\n        white 20%,\n        white 80%,\n        transparent\n    );\n    background-color: #03030345;\n}\n\n#fav-city .city-list-item + .city-list-item,\n.day-list-item + .day-list-item {\n    padding: 0 0.8rem;\n}\n.city-list-item:hover,\n.day-list-item:hover {\n    background-color: rgba(128, 128, 128, 0.343);\n    cursor: pointer;\n    transform: translate(0, 0) scale(1.009);\n    transition: all 0.3s ease-in-out;\n}\n.city-list-item:hover {\n    background-color: #020202;\n}\n.city-list-item:hover {\n}\n.city-list-item {\n    position: relative;\n}\n.city-list-item .delete-location {\n    position: absolute;\n}\n.city-list-item .delete-location {\n    background: none;\n    border: none;\n    right: 0.5rem;\n    to